import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController } from 'ionic-angular';
import { AngularFireDatabase, AngularFireList } from 'angularfire2/database';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { GlobalProvider } from '../../providers/global/global';

import { AccountPage } from '../account/account';

@IonicPage()
@Component({
  selector: 'page-categories',
  templateUrl: 'categories.html',
})
export class CategoriesPage {

  categoriesRef : AngularFireList<any>;
  categories    : Observable<any[]>;

  subCategoriesRef : AngularFireList<any>;
  subCategories    : Observable<any[]>;

  catData : any = '';
  
  constructor(public navCtrl: NavController, public db: AngularFireDatabase, public loadingCtrl: LoadingController, private globalService : GlobalProvider) {

    this.categoriesRef = db.list('categories', ref => ref.orderByChild('parentId').equalTo(0))
    this.categories    = this.categoriesRef.valueChanges();

    this.categories.subscribe( action => {
      this.catData = action;
      console.log(action);
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HomePage');
  }

  account() {
    this.navCtrl.push(AccountPage);
    // this.navCtrl.setRoot(AccountPage, {}, { animate: true, direction: "forward" });    
  }

}



